/* CSS 리셋 */
@import './reset.css';
/* 가상선택자 */
:root {
    /* color */
    --primary-100: #26a69a;
    --primary-200: #408d86;
    --primary-300: #cdfaf6;
    --accent-100: #80cbc4;
    --accent-200: #43a49b;
    --text-100: #263339;
    --text-200: #728f9e;
    --bg-100: #e0f2f1;
    --bg-200: #d0ebea;
    --bg-300: #ffffff;
    /* font-size */
    --font-size-huge: 64px;
    --font-size-large: 48px;
    --font-size-medium-large: 36px;
    --font-size-medium: 24px;
    --font-size-small: 18px;
    --font-size-micro: 14px;
    /* margin */
    --margin-very-large: 48px;
    --margin-large: 36px;
    --margin-medium-large: 24px;
    --margin-medium: 12px;
    --margin-small: 8px;
    --margin-micro: 4px;
    /* padding */
    --padding-very-large: 48px;
    --padding-large: 36px;
    --padding-medium-large: 24px;
    --padding-medium: 12px;
    --padding-small: 8px;
    --padding-micro: 4px;
    --padding-double-large: 24px 48px;
    --padding-double-medium: 12px 24px;
    --padding-double-small: 8px 12px;
    --padding-double-micro: 4px 8px;
    /* font-family */
    --font-family-default: 'GmarketSansMedium';
}
/* 폰트 */
@font-face {
    font-family: 'GmarketSansMedium';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff')
        format('woff');
    font-weight: normal;
    font-style: normal;
}

button {
    outline: none;
    border: none;
    background: none;
    cursor: pointer;
}

.img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.visible {
    display: flex;
}

.invisible {
    display: none;
}

.header_icons {
    color: var(--text-100);
    font-size: var(--font-size-medium-large);
    margin-right: var(--margin-small);
    padding: var(--padding-small);
    border-radius: 10px;
}

.header_icons:hover {
    color: var(--primary-200);
}

@media only screen and (min-width: 320px) and (max-width: 768px) {
    :root {
        /* font-size */
        --font-size-huge: 32px;
        --font-size-large: 24px;
        --font-size-medium-large: 18px;
        --font-size-medium: 12px;
        --font-size-small: 10px;
        --font-size-micro: 8px;
        /* margin */
        --margin-very-large: 24px;
        --margin-large: 18px;
        --margin-medium-large: 12px;
        --margin-medium: 6px;
        --margin-small: 4px;
        --margin-micro: 2px;
        /* padding */
        --padding-very-large: 24px;
        --padding-large: 18px;
        --padding-medium-large: 12px;
        --padding-medium: 6px;
        --padding-small: 4px;
        --padding-micro: 2px;
        --padding-double-large: 12px 24px;
        --padding-double-medium: 6px 12px;
        --padding-double-small: 4px 6px;
    }
}
/* Media query --- tablet */
@media only screen and (min-width: 768px) and (max-width: 1024px) {
    :root {
        /* font-size */
        --font-size-huge: 48px;
        --font-size-large: 36px;
        --font-size-medium-large: 27px;
        --font-size-medium: 18px;
        --font-size-small: 15px;
        --font-size-micro: 12px;
        /* margin */
        --margin-very-large: 36px;
        --margin-large: 27px;
        --margin-medium-large: 18px;
        --margin-medium: 9px;
        --margin-small: 6px;
        --margin-micro: 3px;
        /* padding */
        --padding-very-large: 36px;
        --padding-large: 27px;
        --padding-medium-large: 18px;
        --padding-medium: 9px;
        --padding-small: 6px;
        --padding-micro: 3px;
        --padding-double-large: 18px 36px;
        --padding-double-medium: 9px 18px;
        --padding-double-small: 6px 9px;
    }
}
